import { createRouter, createWebHistory } from "vue-router";
import HomePage from "@/views/HomePage";

const routes = [
  {
    path: "/",
    name: "Home",
    component: HomePage,
  },
  {
    path: "/client",
    name: "Client",
    component: () => import("@/views/Client"),
  },
  {
    path: "/portfolio",
    name: "Portfolio",
    children: [
      {
        path: "",
        component: () => import("@/views/Portfolio/Portfolio"),
      },
      {
        path: "logo",
        component: () => import("@/views/Portfolio/Logo"),
      },
      {
        path: "appui",
        component: () => import("@/views/Portfolio/Appui"),
      },
      {
        path: "Website",
        component: () => import("@/views/Portfolio/Website"),
      },
      {
        path: "dashboard",
        component: () => import("@/views/Portfolio/Dashboard"),
      },
      {
        path: "wireframing",
        component: () => import("@/views/Portfolio/Wireframing"),
      },
      {
        path: "appicon",
        component: () => import("@/views/Portfolio/AppIcon"),
      },
      {
        path: "nftapp",
        component: () => import("@/views/Portfolio/NFTApp"),
      },
      {
        path: "saas_ui",
        component: () => import("@/views/Portfolio/SaasUi"),
      },
      {
        path: "webapp",
        component: () => import("@/views/Portfolio/WebApp"),
      },
      {
        path: "banners",
        component: () => import("@/views/Portfolio/Banners"),
      },
      {
        path: "postcard",
        component: () => import("@/views/Portfolio/Postcard"),
      },
    ],
  },
  {
    path: "/service",
    name: "Service",
    component: () => import("@/views/Service"),
  },
];
const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  mode : 'hash',
  routes
});
export default router;
